



























export default {
	name: 'UniCalendarItem',
	props: {
		/**
		 * 当前日期
		 */
		canlender: {
			type: null,
			default: () => {
				return {}
			}
		},
		lunar: {
			type: Boolean,
			default: false
		},
		//设置多选默认颜色
		selectColor:{
			type:String,
			default:'#1CBBB4'
		}
	},
	data() {
		return {}
	},
	created() {},
	methods: {
		selectDays(week, index, ischeck, isDay, lunar) {
			this.$emit('selectDays', {
				week,
				index,
				ischeck,
				isDay,
				lunar
			})
		}
	}
}
